<template>
  <div class="login_page">
    <div class="login-page-inner">
      <div class="input-text-wrapper">
        <el-form :model="userForm" :rules="rules" ref="ruleForm"  class="demo-ruleForm" label-width="100px">
          <el-form-item label="" prop="name">
            <el-input v-model="userForm.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="userId">
            <el-input v-model="userForm.userId" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input v-model="userForm.sex" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model="userForm.age" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="userForm.userName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="passWord">
            <el-input type="password" v-model="userForm.passWord" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="input-text-wrapper marginT30 text-center">
        <el-button type="primary" @click="submit" class="registerBtn"></el-button>
        <router-link class="pull-right" to="/login">
            <el-button type="primary" class="loginBtn"></el-button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        userForm:{
          userName: '',
          passWord: '',
          userId: '',
          sex:'',
          age: '',
          name: ""
        },
        rules:{
          userId: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^[0-9]+$/, message: '' }
          ],
          userName: [
            { required: true, message: '', trigger: 'blur' },
            {min: 2, max: 8, message: ' 2  8 ', trigger: 'blur'}
          ],
          grade: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^[0-9]+$/, message: '' }
          ],
          class: [
            { required: true, message: '', trigger: 'blur' },
            { pattern: /^[0-9]+$/, message: '' }
          ],
          passWord: [
            { required: true, message: '', trigger: 'blur' },
            { min: 6, max: 20, message: '6~20', trigger: 'change' },
            { pattern: /^[A-Za-z0-9]+$/, message: '' }
          ]
        }
      }
    },
    methods: {
      // 
      submit() {
        this.$axios.post('/api/studentregister',{
          userInfo:this.userForm
        }).then(response => {
          let res = response.data;
          if (res.status == '0') {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.$router.push('/')
          }
        }).catch(err => {
          this.$message({
            showClose: true,
            message: '',
            type: 'warning'
          });
        })
      }
    }
  }
</script>

<style scoped>
    .note {
        color: #f7ba2a;
        font-size: 14px;
    }
    /* * */

    .note>p:after {
        content: ' *';
        color: red;
        font-size: 120%;
    }

    .login_page {
        width: 100%;
        height: 100%;
    }

    .login-page-inner {
        max-width: 450px;
        margin: 0 auto;
        padding: 20px;
        z-index: 10;
    }

    #canvas {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
    }

    .loginBtn {
        width: 30%;
        margin-left: 200px;
    }
</style>



