<template>
    <div class="base-information">
        <div class="base-title"></div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm form-border">
            <el-form-item label="1" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="2" prop="sex">
                <el-radio-group v-model="ruleForm.sex">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="3" prop="age">
                <el-input v-model="ruleForm.age"></el-input>
            </el-form-item>
            <el-form-item label="4" prop="telp">
                <el-input v-model="ruleForm.telp"></el-input>
            </el-form-item>
            <el-form-item label="5" prop="education">
                <el-radio-group v-model="ruleForm.education">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="6" prop="community">
                <el-input v-model="ruleForm.community"></el-input>
            </el-form-item>
            <el-form-item prop="type">
                <div class="label-surplus">7?</div>
                <el-checkbox-group v-model="ruleForm.type">
                    <el-checkbox label="" name="type" ></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                    <el-checkbox label="" name="type"></el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item prop="isMotion">
                <div class="label-surplus">8?</div>
                <el-radio-group v-model="ruleForm.isMotion">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="isSmoke">
                <div class="label-surplus">9 :</div>
                <el-radio-group v-model="ruleForm.isSmoke">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="isDrink">
                <div class="label-surplus">10 :</div>
                <el-radio-group v-model="ruleForm.isDrink">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="isMemoryDown">
                <div class="label-surplus">11 :</div>
                <el-radio-group v-model="ruleForm.isMemoryDown">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="ill">
                <div class="label-surplus">12 :</div>
                <el-checkbox-group v-model="ruleForm.ill">
                    <el-checkbox label="" name="ill" ></el-checkbox>
                    <el-checkbox label="" name="ill"></el-checkbox>
                    <el-checkbox label="" name="ill"></el-checkbox>
                    <el-checkbox label="" name="ill"></el-checkbox>
                    <el-checkbox label="" name="ill"></el-checkbox>
                    <el-checkbox label="" name="ill"></el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item prop="desc">
                <div class="label-surplus">13 :</div>
                <el-input type="textarea" v-model="ruleForm.desc" :rows="5"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        ruleForm: {
            name: '',
            type: [],
            ill: [],
            sex: '',
            desc: '',
            age: '',
            telp: '',
            education: '',
            community: '',
            isMotion: '',
            isSmoke: '',
            isDrink: '',
            isMemoryDown: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          type: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          ill: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          sex: [
            { required: true, message: '', trigger: 'change' }
          ],
          age: [
            { required: true, message: '', trigger: 'blur' }
          ],
          telp: [
            { required: true, message: '', trigger: 'blur' }
          ],
          education: [
            { required: true, message: '', trigger: 'change' }
          ],
          community: [
            { required: true, message: '', trigger: 'blur' }
          ],
          isMotion: [
              { required: true, message: '', trigger: 'change' }
          ],
          isSmoke: [
              { required: true, message: '', trigger: 'change' }
          ],
          isDrink: [
              { required: true, message: '', trigger: 'change' }
          ],
          isMemoryDown: [
              { required: true, message: '', trigger: 'change' }
          ],
          desc: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang="scss">
    .base-information {
        .base-title {
            width: 300px;
            height: 40px;
            line-height: 40px;
            margin: 0 auto;
            padding-top: 30px; 
            font-size: 26px;
        }   
        
        .label-surplus {
            margin-left: -90px;
        }

        .form-border {
            border: 1px solid #ccc;
            margin: 40px 100px;
            padding: 20px;
            background: #fff;
        }
    }

</style>




